import React, {useCallback, useEffect, useState} from 'react';
import Head from 'next/head';
import AppLayout from '../components/AppLayout';
import { Button, Checkbox, Form, Input } from 'antd';
import useInput from '../hooks/useInput';
import styled from 'styled-components';
import { useDispatch, useSelector } from 'react-redux';
import { LOAD_MY_INFO_REQUEST, SIGN_UP_REQUEST } from '../reducers/user';
import Router from 'next/router';
import wrapper from '../store/configureStore';
import { END } from 'redux-saga';
import axios from 'axios';

const ErrorMessage = styled.div`
  color:red;
`

const Signup = () => {
  const dispatch = useDispatch();
  const { signUpLoading, signUpDone, signUpError,me } = useSelector((state) => state.user);

  useEffect(() => {
    if(signUpDone) {
      Router.replace('/');
    }
  }, [signUpDone]);

  useEffect(() => {
    if(signUpError) {
      alert(signUpError);
    }
  }, [signUpError]);

  useEffect(() => {
    if(me && me.id) {
      Router.replace('/');
    }
  }, [me&& me.id]);

  const [email, onChangeEmail] = useInput('');
  const [nickname, onChangeNickname] = useInput('');
  const [password, onChangePassword] = useInput('');
  const [passwordCheck, setPasswordCheck] = useState('');
  const [passwordError, setPasswordError] = useState(false);

  const onChangePasswordCheck = useCallback((e) => {
    setPasswordCheck(e.target.value);
    setPasswordError(e.target.value !== password);
  }, [password]);

  const [term, setTerm] = useState('');
  const [termError, setTermError] = useState(false);
  const onChangeTerm = useCallback((e) => {
    setTerm(e.target.checked);
    setTermError(false);
  },[]);

  const onSubmit = useCallback(()=> {
    if(password !== passwordCheck) {
      return setPasswordError(true);
    }
    if(!term) {
      return setTermError(true);
    }
    dispatch({
      type: SIGN_UP_REQUEST,
      data: { email, password, nickname}
    })
  }, [email, password, passwordCheck, term])
  

  return (
    <AppLayout>
      <Head>
        <title> | NodeBird</title>
      </Head>
      <Form onFinish={onSubmit}>
        <div>
          <label htmlFor='user-email'></label>
          <br />
          <Input name='user-email' type="email" value={email} onChange={onChangeEmail} required />
        </div>
        <div>
          <label htmlFor='user-nick'></label>
          <br />
          <Input name='user-nick' value={nickname} onChange={onChangeNickname} required />
        </div>
        <div>
          <label htmlFor='user-password'></label>
          <br />
          <Input name='user-password' value={password} type="password" onChange={onChangePassword} required />
        </div>
        <div>
          <label htmlFor='user-password-check'></label>
          <br />
          <Input name='user-password-check' type="password" value={passwordCheck} onChange={onChangePasswordCheck} required />
          {passwordError && <ErrorMessage>  .</ErrorMessage> }
        </div>
        <div>
          <Checkbox name="user-term" checked={term} onChange={onChangeTerm} >   .</Checkbox>
          {termError && <ErrorMessage>  .</ErrorMessage>}
        </div>
        <div style={{marginTop: 10}}>
          <Button type='primary' htmlType='submit' loading={signUpLoading}></Button>
        </div>
      </Form>
    </AppLayout>
  );
};

export const getServerSideProps = wrapper.getServerSideProps(async (context) =>  {
  const cookie = context.req ? context.req.headers.cookie : '';
  axios.defaults.headers.Cookie= '';
  if(context.req && cookie) {
    axios.defaults.headers.Cookie= cookie;
  }
  context.store.dispatch({
    type: LOAD_MY_INFO_REQUEST,
  })
  context.store.dispatch(END)
    await context.store.sagaTask.toPromise();
  
})

export default Signup;